.selectStyle {
  font-family: "Rubik-Regular";
  font-size: 14px;
  letter-spacing: -0.18px;
  box-sizing: border-box;
  border-radius: 3px;
  background-color: rgba(69, 88, 98, 0.1);
  padding: 10px 12px !important;
  width: 100%;
  border: 2px solid transparent;
  &:focus {
    border-color: #00bdf7;
  }
}
