.button {
  width: 100%;
  border: 0 none;
  border-radius: 50px;
  padding: 11px 0;
  color: #fff;
  font-weight: bold;
  font-family: Rubik-Medium;

  &:hover {
    background-color: #00B0E5;
  }

  &--Await {
    background-color: #ABE6FC;
    &:hover {
      background-color: #ABE6FC;
    }
  }

  &--noAwait {
    background-color: #00BDF7;
  }

  &--grey{
    background-color: #949494;
  }

  &:focus {
    outline: 1px dashed #000;
  }

}
