.bodyEditSelfUser {
  width: 100%;
  &__nameText {
    font-family: Rubik-Medium, Arial, Helvetica, sans-serif;
    font-size: 20px;
  }
  &__greyText,
  a {
    color: #949494;
  }
  &__fontSize {
    font-size: 14px;
  }
  &__dividerLines {
    padding: 22px 0 4px 0;
    border-bottom: solid 1px #f3f4f6;
    &--inFocus {
      border-bottom: solid 1px skyblue;
    }
  }
  a {
    &:hover {
      color: #000;
    }
  }
  .iconLogout {
    position: relative;
    top: -2px;
    left: -5px;
  }
  .inputOcult {
    cursor: pointer;
    border: 0 solid transparent;
    &:focus {
      outline: none;
    }
  }
}
